body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
}

td, th {
    border: 1px solid #dddddd;
    text-align: left;
    padding: 8px;
}

.input {
  width: 100%;
  height: 100%;
}

.wrapper {
  width: 50%;
  margin: auto;
  margin-top: 20vh;
}



